import { DarkThemeMap } from "twitch-core-ui-tokens";
/**
 * Utility for generating a fallback-compatible CSS ruleset with an arbitrary
 * property paired with a theme token. Generates the fallback rule based on the
 * dark theme, which is the default theme.
 */

export function themeTokenRule(property, themeToken) {
  return "\n    " + property + ": " + DarkThemeMap[themeToken] + ";\n    " + property + ": var(--" + themeToken + ");\n  ";
}